import { useEffect, useState } from 'react';
import { styled } from '@mui/material/styles';
import { Box } from '@mui/material';

const VirtualCursor = () => {
    const [dotPosition, setDotPosition] = useState({ x: 0, y: 0 });
    const [ringPosition, setRingPosition] = useState({ x: 0, y: 0 });
    const [targetPosition, setTargetPosition] = useState({ x: 0, y: 0 });
    const [isVisible, setIsVisible] = useState(false);
    const [isClicking, setIsClicking] = useState(false);
    const [isHovering, setIsHovering] = useState(false);

    useEffect(() => {
        let animationFrameId: number;
        
        const animate = () => {
            // Dot follows slower (0.08 speed)
            setDotPosition(prev => ({
                x: prev.x + (targetPosition.x - prev.x) * 0.1,
                y: prev.y + (targetPosition.y - prev.y) * 0.1
            }));

            // Ring follows faster (0.15 speed)
            setRingPosition(prev => ({
                x: prev.x + (targetPosition.x - prev.x) * 0.15,
                y: prev.y + (targetPosition.y - prev.y) * 0.15
            }));

            animationFrameId = requestAnimationFrame(animate);
        };

        animationFrameId = requestAnimationFrame(animate);
        return () => cancelAnimationFrame(animationFrameId);
    }, [targetPosition]);

    useEffect(() => {
        const updateCursorPosition = (e: MouseEvent) => {
            setTargetPosition({ x: e.clientX, y: e.clientY });
            if (!isVisible) setIsVisible(true);

            // Check if hovering over clickable elements
            const target = e.target as HTMLElement;
            setIsHovering(
                target.tagName === 'BUTTON' ||
                target.tagName === 'A' ||
                target.closest('button') !== null ||
                target.closest('a') !== null ||
                target.classList.contains('clickable')
            );
        };

        const handleMouseDown = () => setIsClicking(true);
        const handleMouseUp = () => setIsClicking(false);
        const handleMouseLeave = () => setIsVisible(false);
        const handleMouseEnter = () => setIsVisible(true);

        window.addEventListener('mousemove', updateCursorPosition);
        window.addEventListener('mousedown', handleMouseDown);
        window.addEventListener('mouseup', handleMouseUp);
        document.addEventListener('mouseleave', handleMouseLeave);
        document.addEventListener('mouseenter', handleMouseEnter);

        return () => {
            window.removeEventListener('mousemove', updateCursorPosition);
            window.removeEventListener('mousedown', handleMouseDown);
            window.removeEventListener('mouseup', handleMouseUp);
            document.removeEventListener('mouseleave', handleMouseLeave);
            document.removeEventListener('mouseenter', handleMouseEnter);
        };
    }, [isVisible]);

    return (
        <>
            <CursorRing
                className={`${isClicking ? 'clicking' : ''} ${isHovering ? 'hovering' : ''}`}
                style={{
                    transform: `translate(${ringPosition.x}px, ${ringPosition.y}px)`,
                    opacity: isVisible ? 1 : 0
                }}
            />
            <CursorDot
                className={`${isClicking ? 'clicking' : ''} ${isHovering ? 'hovering' : ''}`}
                style={{
                    transform: `translate(${dotPosition.x}px, ${dotPosition.y}px)`,
                    opacity: isVisible ? 1 : 0
                }}
            />
        </>
    );
};

const CursorRing = styled(Box)(({ theme }) => ({
    position: 'fixed',
    top: -25,
    left: -25,
    width: 50,
    height: 50,
    border: `2px solid ${theme.palette.common.white}`,
    borderRadius: '50%',
    pointerEvents: 'none',
    zIndex: 9999,
    transition: 'all 0.3s ease-out',
    mixBlendMode: 'difference',

    '&.hovering': {
        width: 60,
        height: 60,
        top: -30,
        left: -30,
    },

    '&.clicking': {
        transform: 'scale(0.9)',
    },

    '@media (hover: none)': {
        display: 'none',
    }
}));

const CursorDot = styled(Box)(({ theme }) => ({
    position: 'fixed',
    top: -4,
    left: -4,
    width: 8,
    height: 8,
    backgroundColor: theme.palette.common.white,
    borderRadius: '50%',
    pointerEvents: 'none',
    zIndex: 9999,
    transition: 'transform 0.3s ease-out',
    mixBlendMode: 'difference',

    '&.hovering': {
        width: 8,
        height: 8,
        top: -4,
        left: -4,
    },

    '&.clicking': {
        transform: 'scale(0.9)',
    },

    '@media (hover: none)': {
        display: 'none',
    }
}));

export { VirtualCursor }; 