import { styled } from "@mui/material/styles";
import { Box, Container, Grid, Typography } from "@mui/material";

import { AnimatedElement } from "../../components/animation/AnimatedElement";
import { SectionTitle } from '../../components/SectionTitle';
import { topGameImg1, topGameImg2, topGameImg3, topGameImg4, topGameImg5, topGameImg6 } from "../../assets/image";

const games = [
    { img: topGameImg1 },
    { img: topGameImg2 },
    { img: topGameImg3 },
    { img: topGameImg4 },
    { img: topGameImg5 },
    { img: topGameImg6 },
]
const TopGame = () => {
    return (
        <TopGameWrapper id="poster">
            <Container maxWidth="xl">
                <AnimatedElement animation="fadeInUp" delay={0.1}>
                    <SectionTitle>
                        Top game hot tại Vip52
                    </SectionTitle>
                    <Grid container spacing={2} justifyContent="center" alignItems="center" style={{ padding: "30px 0px" }}>
                        {games.map((item, key) => (
                            <Grid item xs={6} sm={6} md={2} key={key}>
                                <GameItem>
                                    <Box component={"img"} src={item.img} sx={{ width: "100%", height: "auto" }} />
                                    <LinkButton>Chơi ngay</LinkButton>
                                </GameItem>
                            </Grid>
                        ))}
                    </Grid>
                </AnimatedElement>
            </Container>
        </TopGameWrapper>
    );
};

const TopGameWrapper = styled(Box)(({ theme }) => ({
    paddingTop: '30px',
    width: '100%',
    height: '100%',
}));

const GameItem = styled(Box)(({ theme }) => ({
    display: "flex",
    flexDirection: "column",
    width: "100%",
    justifyContent: "center",
    alignItems: "center"
}))

const LinkButton = styled(Box)(({ theme }) => ({
    borderRadius: "99px",
    border: "2px solid #7a9c59",
    color: "#fff",
    transition: "all 0.3s",
    padding: "4px 15px",
    userSelect: "none",
    cursor: "pointer",
    '&:hover': {
        backgroundColor: "#7a9c59",
    }
}))
export { TopGame };