import { Box } from "@mui/material";
import styled from "styled-components";

const SectionTitle = ({ children }: { children: string }) => {
    return (
        <SectionWrapper>
            <BBox />
            <TitleContainer>
                {children}
            </TitleContainer>
            <BBox />
        </SectionWrapper>
    );
};

const SectionWrapper = styled(Box)(() => ({
    position: 'relative',
    textAlign: 'center',
    padding: '5px 20px',
    background: `linear-gradient(to right, transparent 0%, #FFBBFF 20%, #FFBBFF 80%, transparent 100%)`,
    clipPath: `polygon(10% 0%, 90% 0%, 100% 50%, 90% 100%, 10% 100%, 0% 50%)`,
    display: 'flex',
    flexDirection: 'row',
    alignItems: "center",
    justifyContent: 'space-between',
    width: '100%',
}));

const BBox = styled('b')(() => ({
    backgroundColor: 'white',
    display: 'block',
    flex: 1,
    height: '2px',
    opacity: 0.1,
}));

const TitleContainer = styled(Box)(() => ({
    textAlign: 'center',
    background: `linear-gradient(0deg, #9B30FF, #538ac6)`,
    border: `1px solid #ff8221!important`,
    padding: '5px 45px',
    clipPath: 'polygon(10% 0%, 90% 0%, 100% 50%, 90% 100%, 10% 100%, 0% 50%)',
    color: 'white',
    fontWeight: "bold",
    fontSize: "18px",
    zIndex: 1, // Ensure the title is above the BBox  
}));

export { SectionTitle };