import { styled } from "@mui/material/styles";
import { Box, Container, Grid, Typography } from "@mui/material";

import { AnimatedElement } from "../../components/animation/AnimatedElement";
import { SectionTitle } from '../../components/SectionTitle';
import { popularGame1, popularGame2, popularGame3, popularGame4, popularGame5, popularGame6, popularGame7, popularGame8, popularGame9, topGameImg2 } from "../../assets/image";

import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import { Autoplay, Pagination, Navigation } from 'swiper/modules';

const data = [
    [popularGame1,
        popularGame2,
        popularGame3,
        popularGame4,
        popularGame5,
        popularGame6],
    [
        popularGame4,
        popularGame5,
        popularGame6,
        popularGame7,
        popularGame8,
        popularGame9
    ]
]
const PopularGames = () => {
    return (
        <TopGameWrapper id="poster">
            <Container maxWidth="xl">
                <AnimatedElement animation="fadeInUp" delay={0.1}>
                    <SectionTitle>
                        Thông tin chung về cổng game Vip52
                    </SectionTitle>
                    <Swiper
                        slidesPerView={1}
                        centeredSlides={true}
                        spaceBetween={0}
                        navigation={true}
                        autoplay={{
                            delay: 2500,
                            disableOnInteraction: false,
                        }}
                        modules={[Autoplay, Pagination, Navigation]}
                        className="mySwiper"
                        pagination={{
                            clickable: true,
                        }}
                    >
                        {data.map((imgs, key) => {
                            return <SwiperSlide key={key}>
                                <Grid container spacing={2} justifyContent="center" alignItems="center" mt={4} style={{ padding: "30px 0px" }}>
                                    {imgs.map((img, key) => (
                                        <Grid item xs={6} sm={6} md={2} key={key}>
                                            <GameItem>
                                                <Box component={"img"} src={img} sx={{ width: "100%", height: "auto" }} />
                                                <LinkButton>Chơi ngay</LinkButton>
                                            </GameItem>
                                        </Grid>
                                    ))}
                                </Grid>
                            </SwiperSlide>
                        })}
                    </Swiper>
                </AnimatedElement>
            </Container>
        </TopGameWrapper>
    );
};

const TopGameWrapper = styled(Box)(({ theme }) => ({
    paddingTop: '30px',
    width: '100%',
    height: '100%',
}));

const GameItem = styled(Box)(({ theme }) => ({
    display: "flex",
    flexDirection: "column",
    width: "100%",
    justifyContent: "center",
    alignItems: "center"
}))

const LinkButton = styled(Box)(({ theme }) => ({
    borderRadius: "99px",
    border: "2px solid #7a9c59",
    color: "#fff",
    transition: "all 0.3s",
    padding: "4px 15px",
    userSelect: "none",
    cursor: "pointer",
    '&:hover': {
        backgroundColor: "#7a9c59",
    }
}))
export { PopularGames };