import { styled } from "@mui/material/styles";
import { Box, Container, Typography } from "@mui/material";

import { AnimatedElement } from "../../components/animation/AnimatedElement";
import { SectionTitle } from '../../components/SectionTitle';

const Promition = () => {
    return (
        <PromitionWrapper >
            <Container maxWidth="xl">
                <AnimatedElement animation="fadeInUp" delay={0.1}>
                    <SectionTitle>
                        VIP52 - GAME BÀI VIP52 CLUB - TẢI GAME VIP52 NHẬN CODE 100K
                    </SectionTitle>
                    <ContentWrapper>
                        Vip52 là cổng game bài đổi thưởng hàng đầu, nổi bật với giao diện hiện đại và luật chơi cập nhật mới nhất.
                        Suốt 4 năm qua, Vip52 đã khẳng định vị thế là lựa chọn hàng đầu của cộng đồng game thủ Việt.
                        Được ủy quyền chính thức và sở hữu độc quyền nhiều trò chơi hấp dẫn, Vip52.org mang đến trải nghiệm giải trí đỉnh cao.
                        Khám phá thêm thông tin chi tiết ngay dưới đây!
                    </ContentWrapper>
                </AnimatedElement>
            </Container>
        </PromitionWrapper>
    );
};

const PromitionWrapper = styled(Box)(({ theme }) => ({
    paddingTop: '30px',
    width: '100%',
    height: '100%',
}));

const ContentWrapper = styled(Box)(({ theme }) => ({
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'space-evenly',
    gap: theme.spacing(8),
    padding: `${theme.spacing(3)} 0 0 0`,
    color: "#ffffff",

    [theme.breakpoints.down('md')]: {
        gap: theme.spacing(5),
        minHeight: "unset",
    }
}));

export { Promition };