import { styled } from "@mui/material/styles";
import { Box } from "@mui/material";

import { useScrollAnimation } from "../../utils/scrollAnimation";

import { Swiper, SwiperSlide } from 'swiper/react';

import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';

import { bannerImg1, bannerImg2, bannerImg3, bannerImg4 } from "../../assets/image";
import { Pagination, Navigation } from 'swiper/modules';

const data = [
  { image: bannerImg1 },
  { image: bannerImg2 },
  { image: bannerImg3 },
  { image: bannerImg4 },
];

const Banner = () => {

  return (
    <BannerWrapper id="banner">
      <style>{pageNationStyle}</style>
      <Swiper
        slidesPerView={1}
        spaceBetween={0}
        loop={true}
        pagination={{
          clickable: true,
        }}
        navigation={{
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        }}
        modules={[Pagination, Navigation]}
        className="mySwiper"
        style={{
          // maxHeight: "448.516px",
          position: "relative"
        }}
      >
        {data.map((s, key) => {
          return <SwiperSlide key={key}>
            <BanerImg src={s.image} />
          </SwiperSlide>
        })}
        <div className="swiper-button-prev">
        </div>
        <div className="swiper-button-next">
        </div>
      </Swiper>
    </BannerWrapper>
  );
};

// Enhanced styled components
const BannerWrapper = styled(Box)(({ theme }) => ({
  width: '100%',
  height: 'auto',
  position: 'relative',
  overflow: 'hidden',
  backgroundRepeat: 'no-repeat',
  transition: 'all 0.6s ease-out',
}));


const BanerImg = styled('img')(({ theme }) => ({
  height: "100%",
  maxWidth: "100%"
}));

const pageNationStyle = `  
:root {  
  --swiper-navigation-size: 20px; /* Adjust this to your desired size */  
}  

.swiper-pagination {  
  bottom: 10px; /* Positioning */  
}  

.swiper-pagination-bullet {  
  border: 1px solid rgb(145, 143, 143);  
  background: rgba(0, 0, 0, 0);  
  width: 12px; /* Set the bullet width */  
  height: 12px; /* Set the bullet height */  
}  

.swiper-pagination-bullet-active {  
  background: rgb(224, 224, 224); /* Color of active bullet */  
  transform: scale(0.8); /* Scale for active bullet */  
}  

.swiper-button-prev,  
.swiper-button-next {  
  width: 33px;  /* Adjust to your preference */  
  height: 33px; /* Adjust to your preference */  
  display: flex;  
  align-items: center;  
  justify-content: center;  
  color: #ffffff;  /* Change this depending on the desired color */  
  background: rgba(0, 0, 0, 0.5);  /* Semi-transparent dark background */  
  border-radius: 50%;  /* Circular buttons */  
  border: 1px solid #ffffff; /* Button border */  
  box-shadow: 0 0 8px rgba(255, 255, 255, 0.8); /* Glowing effect */  
  transition: background-color 0.3s, box-shadow 0.3s;  
  cursor: pointer;  /* Pointer cursor */  
}  

/* Hover effects */  
.swiper-button-prev:hover,  
.swiper-button-next:hover {  
  background: rgba(255, 255, 255, 0.6); /* Brighten background on hover */  
  box-shadow: 0 0 16px rgba(255, 255, 255, 1); /* Enhance glow */  
}  

/* Hide navigation on mobile */  
@media (max-width: 768px) {  
  .swiper-button-prev,  
  .swiper-button-next {  
    display: none; /* Hides buttons on mobile */  
  }  
}  
`;
export { Banner };