import React from 'react';
import { styled } from "@mui/material/styles";
import { Box, Button, Container, Grid } from "@mui/material";
import { banwebImg, googleStoreImg, iosImg, androidImg } from '../../assets/image';
import { transform } from 'typescript';



const Stores = () => {
    return (
        <ContactWrapper>
            <Container maxWidth="xl">
                <Grid container spacing={2} justifyContent="center" alignItems="center" mt={4}>
                    <Grid item xs={6} sm={6} md={3}>
                        <StyledButton src={banwebImg} />
                    </Grid>
                    <Grid item xs={6} sm={6} md={3}>
                        <StyledButton src={googleStoreImg} />
                    </Grid>
                    <Grid item xs={6} sm={6} md={3}>
                        <StyledButton src={iosImg} />
                    </Grid>
                    <Grid item xs={6} sm={6} md={3}>
                        <StyledButton src={androidImg} />
                    </Grid>
                </Grid>
            </Container>
        </ContactWrapper>
    );
};

const ContactWrapper = styled(Box)(({ theme }) => ({
    paddingTop: "0px"
}))

const StyledButton = styled('img')(({ theme }) => ({
    color: '#fff',
    transition: "all 0.3s",
    borderRadius: '12px',
    textTransform: 'none',
    padding: '10px 20px',
    width: '100%', // Make button full width in the grid  
    // boxShadow: '0px 4px 20px rgba(0, 0, 0, 0.3)',
    '&:hover': {
        transform: 'scale(1.1)',
    },
}));
export { Stores };