const IntroductionWrapper = styled(Box)(({ theme }) => ({
    paddingTop: '30px',
    width: '100%',
    height: '100%',
}));

const ContentWrapper = styled(Box)(({ theme }) => ({
    display: 'flex',
    marginTop: "20px",
    border: "1.5px solid rgb(141, 79, 211)",
    flexDirection: 'column',
    justifyContent: 'space-evenly',
    padding: `15px`,
    maxHeight: "500px",
    overflowY: "auto",
    color: "#ffffff",
    backgroundColor: "rgb(33, 21, 37)"
}));

export { Introduction };