import React from "react";
import { ThemeProvider, createTheme } from "@mui/material/styles";

declare module '@mui/material/styles' {
  interface BreakpointOverrides {
    xs: true
    sx: true
    sm: true
    md: true
    lg: true
    xl: true
    xl2: true
  }

  interface CommonColors {
    mainBg: string
    headerShadow: string
    
    boxBg: string
    boxBorder: string
    footerBg: string

    firstButtonBg: string
    secondButtonBg: string
    thirdButtonBg: string

    firstGradient: string
    secondGradient: string
    thirdGradient: string
    fourthGradient: string

    firstTypography: string
    secondTypography: string
    thirdTypography: string
    fourthTypography: string
  }

  // interface Palette {
  //   mainBg: PaletteColor;
  // }

  // interface PaletteOptions {
  //   mainBg: PaletteColorOptions;
  // }
}

declare module '@mui/material/Button' {
  interface ButtonPropsColorOverrides {
    // mainBg: true;
  }
}

const MuiThemeProvider = ({ children }: any) => {
  const theme = createTheme({
    breakpoints: {
      values: {
        xs: 0,
        sx: 450,
        sm: 640,
        md: 768,
        lg: 1024,
        xl: 1280,
        xl2: 1536,
      }
    },
    palette: {
      mode: "dark",
      common: {
        black: '#000',
        white: '#fff',

        mainBg: '#10100f',
        headerShadow: '#ffffff14',
        footerBg: '#10100f',
        
        boxBg: '#121212',
        boxBorder: '#90caf920',

        firstButtonBg: '#ffe81a',
        secondButtonBg: '#ce93d8',
        thirdButtonBg: '#42a5f5',

        firstGradient: '#ffe81a',
        secondGradient: '#ce93d8',
        thirdGradient: '#42a5f5',
        fourthGradient: '#ffe81a',

        firstTypography: '#ffffff',
        secondTypography: '#ffffffb3',
        thirdTypography: '#ffe81a',
        fourthTypography: '#ce93d8',
      },

      // mainBg: { main: '#141416', light: '#24262f', contrastText: "#fff" },
      // primary: { main: '#fff', light: '#f55b00', dark: '#1e0500', contrastText: 'rgba(0,0,0,0.87)' },
    },
    typography: {
      allVariants: { color: "#ffffff" },
      htmlFontSize: 16,
      fontSize: 16,
      fontWeightLight: 300,
      fontWeightRegular: 400,
      fontWeightMedium: 500,
      fontWeightBold: 800,

      fontFamily: "ui-sans-serif, system-ui"
    },
  })

  theme.typography.h1 = {
    ...theme.typography.h1,

    fontSize: 50,
    fontWeight: 500,

    [theme.breakpoints.down('lg')]: {
      fontSize: 45,
    },

    [theme.breakpoints.down('md')]: {
      fontSize: 40,
    },

    [theme.breakpoints.down('sm')]: {
      fontSize: 35,
    },
  }

  theme.typography.h2 = {
    ...theme.typography.h2,

    fontSize: 40,
    fontWeight: 500,

    [theme.breakpoints.down('lg')]: {
      fontSize: 35,
    },

    [theme.breakpoints.down('md')]: {
      fontSize: 30,
    },
  }

  theme.typography.h3 = {
    ...theme.typography.h3,

    fontSize: 35,
    fontWeight: 500,

    [theme.breakpoints.down('lg')]: {
      fontSize: 25,
    },

    [theme.breakpoints.down('md')]: {
      fontSize: 20,
    },
  }

  theme.typography.h4 = {
    ...theme.typography.h4,

    fontSize: 30,
    fontWeight: 500,

    [theme.breakpoints.down('lg')]: {
      fontSize: 25,
    },

    [theme.breakpoints.down('md')]: {
      fontSize: 20,
    },
  }

  theme.typography.h5 = {
    ...theme.typography.h5,

    fontSize: 24,
    fontWeight: 500,

    [theme.breakpoints.down('lg')]: {
      fontSize: 20,
    },

    [theme.breakpoints.down('md')]: {
      fontSize: 16,
    },
  }

  theme.typography.h6 = {
    ...theme.typography.h6,

    fontSize: 20,
    fontWeight: 500,

    [theme.breakpoints.down('lg')]: {
      fontSize: 18,
    },

    [theme.breakpoints.down('md')]: {
      fontSize: 16,
    },
  }

  theme.typography.body1 = {
    ...theme.typography.body1,

    fontSize: 18,
    fontWeight: 500,

    [theme.breakpoints.down('lg')]: {
      fontSize: 16,
    },

    [theme.breakpoints.down('md')]: {
      fontSize: 15,
    },
  }

  theme.typography.body2 = {
    ...theme.typography.body2,

    fontSize: 16,
    fontWeight: 500,

    [theme.breakpoints.down('lg')]: {
      fontSize: 15,
    },

    [theme.breakpoints.down('md')]: {
      fontSize: 14,
    },
  }

  return (
    <ThemeProvider theme={theme}>
      {children}
    </ThemeProvider>
  )
}

export { MuiThemeProvider };