import { styled } from "@mui/material/styles";
import { Box, Container, Grid, Typography } from "@mui/material";

import { AnimatedElement } from "../../components/animation/AnimatedElement";
import { SectionTitle } from '../../components/SectionTitle';
import { guideImg1, guideImg10, guideImg11, guideImg12, guideImg2, guideImg3, guideImg4, guideImg5, guideImg6, guideImg7, guideImg8, guideImg9 } from "../../assets/image";

const games = [
    { img: guideImg1, description: 'Hướng dẫn cách nhận giftcode Vip52 dành cho tân thủ' },
    { img: guideImg2, description: 'Tài xỉu Vip52 – Sân chơi cá cược tài xỉu uy tín xanh chính' },
    { img: guideImg3, description: 'Lô đề Vip52 – Sân chơi lô đề trực tuyến có tỷ lệ thắng cao' },
    { img: guideImg4, description: 'Game bắn cá Vip52 – Khám phá đại dương săn ngay hũ lớn' },
    { img: guideImg5, description: 'Game nổ hũ Vip52 – Sân chơi quay slot nổ hũ tỷ lệ thắng cao' },
    { img: guideImg6, description: 'Live casino Vip52 – Sòng bạc trực tuyến đẳng cấp nhất 2024' },
    { img: guideImg7, description: 'Game bài Vip52 – Sảnh game bài trực tuyến uy tín nhất 2024' },
    { img: guideImg8, description: 'Thể thao Vip52 – Sân chơi cá cược bóng đá đa dạng tỷ lệ kèo' },
    { img: guideImg9, description: 'Hướng dẫn quy trình thực hiện rút tiền Vip52 chi tiết nhất' },
    { img: guideImg10, description: 'Hướng dẫn quy trình nạp tiền Vip52 chi tiết cho tân thủ' },
    { img: guideImg11, description: 'Hướng dẫn cách đăng nhập Vip52 chi tiết và nhanh chóng' },
    { img: guideImg12, description: 'Hướng dẫn đăng ký Vip52 chi tiết dành cho tân thủ' },

]
const BettingGuide = () => {
    return (
        <TopGameWrapper id="poster">
            <Container maxWidth="xl">
                <AnimatedElement animation="fadeInUp" delay={0.1}>
                    <SectionTitle>
                        Hướng dẫn cược Vip52
                    </SectionTitle>
                    <Grid container spacing={2} justifyContent="center" alignItems="center" style={{ padding: "30px 0px" }}>
                        {games.map((item, key) => (
                            <Grid item xs={6} sm={6} md={3} key={key}>
                                <GameItem>
                                    <Box component={"img"} src={item.img} sx={{ width: "100%", height: "auto" }} />
                                    <ContextWrapper>{item.description}</ContextWrapper>
                                    <LinkButton>Xem thêm</LinkButton>
                                    <PostDate>
                                        <Box fontSize={13}>11</Box>
                                        <Box fontSize={12}>Nov</Box>
                                    </PostDate>
                                </GameItem>
                            </Grid>
                        ))}
                    </Grid>
                </AnimatedElement>
            </Container>
        </TopGameWrapper>
    );
};

const TopGameWrapper = styled(Box)(({ theme }) => ({
    paddingTop: '30px',
    width: '100%',
    height: '100%',
}));

const GameItem = styled(Box)(({ theme }) => ({
    display: "flex",
    flexDirection: "column",
    width: "100%",
    justifyContent: "center",
    alignItems: "center",
    position: "relative",
    userSelect: "none",
    cursor: "pointer"
}))

const ContextWrapper = styled(Typography)(() => ({
    color: "white",
    padding: "8px",
    fontSize: "14px",
    textAlign: "center"
}))

const LinkButton = styled(Box)(({ theme }) => ({
    border: "2px solid #7a9c59",
    color: "#fff",
    transition: "all 0.3s",
    padding: "4px 15px",
    userSelect: "none",
    cursor: "pointer",
    '&:hover': {
        backgroundColor: "#7a9c59",
    }
}))

const PostDate = styled(Box)(() => ({
    width: "40px",
    // height: "30px",
    aspectRatio: "1/1",
    border: "2px solid #446084",
    backgroundColor: "white",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
    position: "absolute",
    color: "#446084",
    left: "-10px",
    top: '10px',
    "&:hover": {
        backgroundColor: "#446084",
        color: "white"
    }
}))
export { BettingGuide };